import React from "react";
import {NavLink, Route,} from "react-router-dom";

const Tabs = ({tabs,  match}) => {

  return (
    <section className="section section_tabs">
      <ul>
        {
          tabs.map(
            (tab, index) => {
              return (
                <li key={tab.id}>
                  <NavLink
                    to={`${match.path}/${tabs[index].id}`}
                    className='tabs__tab'
                    activeClassName='tabs__tab-active'
                  >
                    {tabs[index].title}
                  </NavLink>
                </li>
              )
            }
          )}
      </ul>

        <Route path="/tabs/:tabId?" render={({match})=>{
         let currentTabId = match.params.tabId || 'tab-1';
         console.log(currentTabId)
          return(
            <div className="tabs__content">
              {tabs.find(
              tab => tab.id === currentTabId
              ).content}
            </div>
          )
        }} />
    </section>
  )
}

export default Tabs;
